document.onclick = function(e) {
  var e = e || window.event; //
  var elem = e.target || e.srcElement;
  while (elem) { //div
    if ((e.path[2].className && (e.path[2].className.indexOf("is-center") != -1)) || (e.path[0].className && (e.path[0].className.indexOf("layui-layer-content") != -1))) {
      return;
    }//divelse if
    elem = elem.parentNode;
  }
  //
  layer.closeAll();
};