<template>
    <section class='_shopClass'>
        <el-dialog
            title=""
            v-dialogDrag
            :visible.sync="tablesThis.shopClassShos">
            <section class='body-section'>
                <el-tabs v-model="activeName" @tab-click="handleClick">
                    <el-tab-pane label="" name="0">
                        <section class='el-tab-section el-tab-section-left'>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                                <el-select slot="prepend" placeholder="" class='_main-rightSelect' v-model='form.activekemuList'>
                                    <el-option label="" value="-1" ></el-option>
                                    <el-option :label="item.sname" :value="item.sid" v-for='item in initDate.kemuList' :key='item.sid'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                                <el-select slot="prepend" placeholder="" class='_main-rightSelect' v-model='form.activekcmuList' >
                                    <el-option label="" value="-1" ></el-option>
                                    <el-option :label="item.cur_name + '(' +(item.tmethods==2?'':'') +')'" :value="item.cur_id" v-for='item in initDate.kcmuList' :key='item.cur_id'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*</span>
                                <el-select slot="prepend" placeholder="" class='_main-rightSelect' v-model='form.activejsSenList'>
                                    <el-option label="" value="-1" ></el-option>
                                    <el-option :label="item.s_name" :value="item.s_id" v-for='item in initDate.jsSenList' :key='item.s_id'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title xin_class'>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                                <el-select slot="prepend" placeholder="" class='_main-rightSelect' v-model='form.activeTeacher'>
                                    <el-option label="" value="-1" ></el-option>
                                    <el-option :label="item.name" :value="item.id" v-for='item in initDate.Teacher' :key='item.id'></el-option>
                                </el-select>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title'>*</span>
                                <el-input placeholder="" v-model="form.chargeStandard"  @paste.native.capture.prevent="handlePaste">
                                    <template slot="append"></template>
                                </el-input>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*</span>
                                <el-input placeholder="" v-model="form.Class_hour" maxlength='5' @paste.native.capture.prevent="handlePaste">
                                    <template slot="append"></template>
                                </el-input>
                            </label>
                            <label class='label-drop-down class-margin-right'>
                                <span class='label-drop-down-title xin_class'>   </span>
                                <el-date-picker
                                    v-model="form.Termvalidity"
                                    type="date"
                                    @change="DATE_Termva"
                                    :editable='false'
                                    format="yyyy-MM-dd"
                                    placeholder="">
                                </el-date-picker>
                            </label>
                            <label class='label-drop-down'>
                                <span class='label-drop-down-title'>*</span>
                                <el-date-picker
                                    v-model="form.thisDate"
                                    type="date"
                                    @change="DATE_change"
                                    :editable='false'
                                    :picker-options="pickerOptions"
                                    placeholder="">
                                </el-date-picker>
                            </label>
                            <label class='label-drop-down label-dis-flex'>
                                <span class='label-drop-down-title xin_class'></span>
                                <el-input disabled placeholder=""></el-input>
                            </label>
                            <label class='label-drop-down label-dis-flex' style='align-items: end;'>
                                <span class='label-drop-down-title xin_class'></span>
                                <el-input
                                    v-model='form.shopClass'
                                    type="textarea"
                                    placeholder=""
                                    show-word-limit
                                    maxlength='500'>
                                </el-input>
                            </label>
                        </section>
                        <section class='el-tab-section el-tab-section-right'>
                            <div class='el-tab-section-label'>
                                <span class='el-tab-section-label-title'>*</span>
                                <div class='radio_div'>
                                    <label class='radio_section' v-for='item in DiscountList' :key='item.id' @click.stop='discountChange(item.id)'>
                                        <input type="radio" :checked='item.f'>
                                        <span>{{item.name}}</span>
                                        <section class='_input_append' v-if='item.id!=1'>
                                            <el-input placeholder="" v-if='item.id==2' @paste.native.capture.prevent="handlePaste" v-model="form.DiscountActive2" :disabled='item.f==false'>
                                                <template slot="append">%</template>
                                            </el-input>
                                            <el-input placeholder="" v-if='item.id==3' @paste.native.capture.prevent="handlePaste" v-model="form.DiscountActive3" :disabled='item.f==false'>
                                                <template slot="append"></template>
                                            </el-input>
                                        </section>
                                    </label>
                                    <p class='radio_section' v-for='items in DiscountKs' :key='items.id'>
                                        <input type="checkbox" :checked='items.f'  @click.stop='DiscountKsChange(items.id)'>
                                        <span>{{items.name}}</span>
                                        <section class='_input_append'>
                                            <el-input placeholder="" :disabled="items.f==false" v-model="form.FreeClassHours" @paste.native.capture.prevent="handlePaste">
                                                <template slot="append"></template>
                                            </el-input>
                                        </section>
                                    </p>
                                </div>
                            </div>
                            <label class='el-tab-section-label el-tab-margin-left'>
                                <span class='personnel'>   <em>{{userLoginInfo.config.contacts}}</em></span>
                                <span class='personnel'>   <em>{{userLoginInfo.config.contacts}}</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left' >
                                <span class='el-tab-section-title'>   </span>
                                <el-input v-model="form.Actual_payment" :readonly='true'>
                                    <template slot="append"></template>
                                </el-input>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left'>
                                <span class='personnel'><em>{{tablesThis.balance}}</em></span>
                                <span class='personnel'><em>: {{form.original_price}}{{form.Discount_mony}}</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append' >
                                <span class='el-tab-section-title'>*</span>
                                <el-select slot="prepend" placeholder="" class='_main-rightSelect' v-model='form.Activemethod'>
                                    <el-option label="" value="-1" ></el-option>
                                    <el-option :label="item.pay_name" :value="item.pay_id" v-for='item in initDate.methodList' :key = 'item.pay_id'></el-option>
                                </el-select>
                            </label>
                        </section>
                    </el-tab-pane>
                    <el-tab-pane label="" name="1">
                        <section class='el-tab-section el-tab-section-left'>
                            <label class='class-shopClass'>
                                <span class='label-drop-down-title'>*&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                                <el-select slot="prepend" v-model="formSetmeal.Setmeal" placeholder="" class='_main-rightSelect' >
                                    <el-option label="" value="0" ></el-option>
                                    <el-option v-for='item in initDate.SetmealList' :key = 'item.meal_id' :label="item.meal_name" :value="item.meal_id"></el-option>
                                </el-select>
                            </label>
                            <div class='div_gulptable'>
                                <el-table
                                    :data="tableData"
                                    stripe
                                    border
                                    style="width: 100%">
                                    <el-table-column
                                    prop="cur_name"
                                    label=""
                                    align='center'
                                    min-width="33.33%">
                                    </el-table-column>
                                    <el-table-column
                                    prop="cur_num"
                                    align='center'
                                    label=""
                                    min-width="33.33%">
                                    </el-table-column>
                                    <el-table-column
                                    prop="cur_value"
                                    align='center'
                                    label=""
                                    min-width="33.33%">
                                    </el-table-column>
                                </el-table>
                            </div>
                            <div class='time_div'>
                                <label class='label-drop-down class-margin-right'>
                                    <span class='label-drop-down-title'>*</span>
                                    <el-input placeholder="" v-model="formSetmeal.Totalprice">
                                        <template slot="append"></template>
                                    </el-input>
                                </label>
                                <label class='label-drop-down '>
                                    <span class='label-drop-down-title'>*</span>
                                    <el-date-picker
                                        v-model="formSetmeal.shopclassTime"
                                        type="date"
                                        :editable='false'
                                        placeholder="">
                                    </el-date-picker>
                                </label>
                            </div>
                            <div class='time_div'>
                                <!-- <label class='label-drop-down class-margin-right'>
                                    <span class='label-drop-down-title xin_class'>   </span>
                                    <el-date-picker
                                        v-model="formSetmeal.TermofvalidityTime"
                                        type="date"
                                        :editable='false'
                                        placeholder="">
                                    </el-date-picker>
                                </label>
                                <label class='label-drop-down '>
                                    <span class='label-drop-down-title'>*</span>
                                    <el-date-picker
                                        v-model="formSetmeal.AttendclassTime"
                                        type="date"
                                        :editable='false'
                                        placeholder="">
                                    </el-date-picker>
                                </label> -->
                            </div>
                            <label class='class-shopClass'>
                                <span class='label-drop-down-title'>*</span>
                                <el-input type='text' placeholder="" disabled></el-input>
                            </label>
                            <label class='class-shopClass textarea-class'>
                                <span class='label-drop-down-title xin_class'></span>
                                <el-input v-model='formSetmeal.remick' maxlength = '500' type='textarea' placeholder=""></el-input>
                            </label>
                        </section>
                        <section class='el-tab-section el-tab-section-right'>
                            <section class='el-tab-section-label'>
                                <span class='el-tab-section-label-title'>*</span>
                                <div class='radio_div'>
                                    <label class='radio_section' v-for='item in DiscountList' :key='item.id' @click.stop='discountChange(item.id)'>
                                        <input type="radio" :checked='item.f'>
                                        <span>{{item.name}}</span>
                                        <section class='_input_append' v-if='item.id!=1'>
                                            <el-input placeholder="" v-if='item.id==2' @paste.native.capture.prevent="handlePaste" v-model="formSetmeal.DiscountActive2" :disabled='item.f==false'>
                                                <template slot="append">%</template>
                                            </el-input>
                                            <el-input placeholder="" v-if='item.id==3' @paste.native.capture.prevent="handlePaste" v-model="formSetmeal.DiscountActive3" :disabled='item.f==false'>
                                                <template slot="append"></template>
                                            </el-input>
                                        </section>
                                    </label>
                                </div>
                            </section>
                            <label class='el-tab-section-label el-tab-margin-left'>
                                <span class='personnel'>   <em>{{userLoginInfo.config.contacts}}</em></span>
                                <span class='personnel'>   <em>{{userLoginInfo.config.contacts}}</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left' >
                                <span class='el-tab-section-title'>   </span>
                                <el-input v-model="formSetmeal.Actual_payment">
                                    <template slot="append"></template>
                                </el-input>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append el-tab-margin-left'>
                                <span class='personnel'><em>{{tablesThis.balance}}</em></span>
                                <span class='personnel'><em>: {{formSetmeal.original_price}}{{formSetmeal.Discount_mony}}</em></span>
                            </label>
                            <label class='el-tab-section-label el-tab-section-append' >
                                <span class='el-tab-section-title'>*</span>
                                <el-select v-model='formSetmeal.Activemethod' slot="prepend" placeholder="" class='_main-rightSelect' >
                                    <el-option label="" value="-1" ></el-option>
                                    <el-option :label="item.pay_name" :value="item.pay_id" v-for='item in initDate.methodList' :key = 'item.pay_id'></el-option>
                                </el-select>
                            </label>
                        </section>
                    </el-tab-pane>
                </el-tabs>
            </section>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="shopClassSave"  size='mini'> </el-button>
                <el-button @click="tablesThis.shopClassShos = false" size='mini'> </el-button>
            </span>
        </el-dialog>
    </section>
</template>
<script>
import {getkmList, getKclist,kcOneByone,CorrespondingT,studentShoopClass,studentSetmeal,studentShoopClassTc} from '../../../../../getserver/server'
import {senioritieList,all_pay_list,get_meals_info} from '../../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
    name: 'Shoppclass',
    data () {
        return {
            pickerOptions:{
                disabledDate(time) {
                    return time.getTime() > Date.now();
                }
            },
            activeName: "0",
            DiscountKs: [{id:4,name: '',f:false}],
            DiscountList: [{id:1,name:'', f:true},{id:2,name:'',f:false},{id:3,name: '',f:false}],
            initDate: {
              //    
              kemuList: [],//  
              jsSenList: [], //   
              kcmuList: [],// 
              Teacher: [], // 
              SetmealList: [],// 
              methodList: [] // 
            },
            form: {
                stu_id: '',// id
                classify: 1,// 1  2
                userid: "",
                activekemuList: "-1",// 
                activejsSenList: "-1", // 
                activekcmuList: "-1", // 
                activeTeacher: '-1',//  id
                chargeStandard: '', // 
                type:'1',//  //  
                type_num: '',
                Class_hour: '',// 
                Termvalidity: '', // 
                thisDate: '', // 
                shopClass:'',// 
                DiscountActive2: '', // 
                DiscountActive3: '', // 
                FreeClassHours: 0, // 
                Actual_payment: '', // 
                Activemethod: '-1',// 
                original_price: '0.00', // 
                Discount_mony: '0.00' // 
            },
            formSetmeal: {
              stu_id: '',// id
              classify: 2,// 1  2
              userid: "",
              type:'1',//  //  
              Setmeal: '0', // id
              Totalprice: '',// 
              shopclassTime: '', // 
              TermofvalidityTime: '',// 
              AttendclassTime: '', // 
              remick: '',// 
              Actual_payment: '', // 
              Activemethod: '-1',// 
              DiscountActive2: '', // 
              DiscountActive3: '', // 
              original_price: '0.00', // 
              Discount_mony: '0.00' // 
            },
            tableData: [],// 
            dialogVisible: true
        }
    },
    inject: ['tablesThis'],
    created(){
      this.getkmList_fun();
      this.all_pay_list_fun();
      this.getStudentSetmeal()
      this.initNewDAte()
      this.form.userid = this.userLoginInfo.id
      this.formSetmeal.userid = this.userLoginInfo.id
    },
    computed:{
      ...mapState(['userLoginInfo'])
    },
    methods: {
        discountChange (id) {
            const _this = this
            this.DiscountList.forEach((data)=>{
                if (data.id == id) {
                    data.f = true
                     if (_this.activeName == 0) {
                        _this.form.type = data.id
                     } else if (_this.activeName == 1) { 
                         _this.formSetmeal.type = data.id
                     }
                } else {
                    data.f = false
                    if (_this.activeName == 0) {
                        _this.form.DiscountActive3 = ""
                        _this.form.DiscountActive2 = ""
                    } else if (_this.activeName == 1) {
                        _this.formSetmeal.DiscountActive3 = ""
                        _this.formSetmeal.DiscountActive2 = ""
                    }
                }
            })
        },
        DiscountKsChange (id) {
            const _this = this
            this.DiscountKs[0].f = !this.DiscountKs[0].f
            if (this.DiscountKs[0].f == false) {
                _this.form.FreeClassHours = ""
            }
        },
        handlePaste(){
          // 
          return false
        },
        initNewDAte () {
            // 
            let date = new Date();
            let YYYY = date.getFullYear(); // 
            let MM = date.getMonth()+1; // 
            let DD = date.getDate(); // 
            if (MM < 10) {
                MM = "0"+ MM
            }
            if (DD < 10) {
                DD = "0" + DD
            }
            this.form.thisDate = YYYY +"-"+ MM + "-" + DD 
        },
        DATE_change (val) {
            // 
            let date = new Date(val);
            let YYYY = date.getFullYear(); // 
            let MM = date.getMonth()+1; // 
            let DD = date.getDate(); // 
            if (MM < 10) {
                MM = "0"+ MM
            }
            if (DD < 10) {
                DD = "0" + DD
            }
            this.form.thisDate = YYYY +"-"+ MM + "-" + DD
        },
        DATE_Termva (val) {
            //  
            let date = new Date(val);
            let YYYY = date.getFullYear(); // 
            let MM = date.getMonth()+1; // 
            let DD = date.getDate(); // 
            if (MM < 10) {
                MM = "0"+ MM
            }
            if (DD < 10) {
                DD = "0" + DD
            }
            this.form.Termvalidity = YYYY +"-"+ MM + "-" + DD
        },
        handleClick(tab, event){
            this.activeName = tab.index
        },
        getStudentSetmeal(){
            const _this = this
            studentSetmeal(this.userLoginInfo.orgid).then((res)=>{
                if (res.data.status) {
                    _this.initDate.SetmealList = res.data.data.data
                }
            })
        },
        shopClassSave(){
            // 
            const _this = this
            // 
            if (this.DiscountList[1].f == true) {
                if (_this.activeName == 0) {
                    this.form.type_num = this.form.DiscountActive2
                } else {
                    this.formSetmeal.type_num = this.formSetmeal.DiscountActive2
                }
            } else if (this.DiscountList[2].f == true) {
                if (_this.activeName == 0) {
                    this.form.type_num = this.form.DiscountActive3
                } else {
                    this.formSetmeal.type_num = this.formSetmeal.DiscountActive2
                }
            } else if (this.DiscountList[0].f == true) {
                if (_this.activeName == 0) {
                    this.form.type_num = ""
                } else {
                    this.formSetmeal.type_num = this.formSetmeal.DiscountActive2
                }
            }
            switch (parseInt(_this.activeName)) {
                case 0:
                    if (this.form.activekemuList == '-1'){
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if ( this.form.activekcmuList == '-1') {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if ( this.form.activejsSenList == '-1') {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if ( this.form.chargeStandard == '') {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if ( this.form.Class_hour == '') {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if ( this.form.thisDate == '') {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if (this.DiscountList[1].f ==true && this.form.DiscountActive2 == "" ) {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return  
                    } else if (this.DiscountList[2].f ==true && this.form.DiscountActive3 == "" ) {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if (this.DiscountKs[0].f == true && this.form.FreeClassHours == "") {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    } else if (this.form.Activemethod == "-1") {
                        this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                        return
                    }
                    _this.form.classify = 1
                    _this.get_studentShoopClass()
                 break;
                case 1:
                    // 
                    if ( _this.formSetmeal.Totalprice == '') {
                        _this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                         return
                    }else if (_this.DiscountList[1].f ==true && _this.formSetmeal.DiscountActive2 == "" ) {
                        _this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        }); 
                         return 
                    } else if (_this.DiscountList[2].f == true && _this.formSetmeal.DiscountActive3 == "" ) {
                        _this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });  
                         return
                    } else if (_this.formSetmeal.Activemethod == "-1") {
                        _this.$message({
                            message: '',
                            type: 'error',
                            duration: 2000,
                        });
                        return
                    }
                    _this.formSetmeal.classify = 2
                    // 
                    _this.get_studentShoopClass()
                break;
            } 
        },
        async all_pay_list_fun () {
            // 
            let resDate = await all_pay_list(this.userLoginInfo.orgid);
            if (resDate.data.status) {
                // 
                this.initDate.methodList = resDate.data.data
            }
        },
        async getkmList_fun () {
            // 
            let resDate = await getkmList();
            if (resDate.data.status) {
                this.initDate.kemuList = resDate.data.data;
            }
        },
        async getKcallList(kmid) {
            //  
            let resDate = await getKclist("","", kmid, "", "", "");
            if (resDate.data.status) {
                this.initDate.kcmuList = resDate.data.data.data;
            }
        },
        async getkcOneByone (curid) {
            let resDate = await kcOneByone(curid)
            if (resDate.data.status) {
              // 
              this.senioritieList_fun(resDate.data.data.t_senioritys.split('/'), resDate.data.data.tqualific.split('/'))
            }
        },
        async senioritieList_fun (a,b){
            //  
            // a id 
            // b  
            let resDate = await senioritieList(this.userLoginInfo.orgid);
            const _this = this
            if (resDate.data.status) {
                let moneyArr = [];
                a.forEach((data,index)=>{
                    b.forEach((datas,i)=>{
                        if (index == i) {
                            moneyArr.push({id: data, money: datas})
                        }
                    })
                })
                moneyArr.forEach((data)=>{
                    resDate.data.data.forEach((item)=>{
                        if (data.id == item.s_id) {
                            item['money'] = data.money
                            _this.initDate.jsSenList.push(item)
                        }
                    })
                })
            }
        },
        async get_CorrespondingT (orgid, curid) {
            //  
            let resDate = await CorrespondingT(orgid,curid)
            if (resDate.data.status) {
                this.initDate.Teacher = resDate.data.data.data;
            }
        },
        async get_studentShoopClass() {
            if (this.activeName == 0) {
                let resDate = await studentShoopClass(this.form)
                if (resDate.data.status) {
                    // 
                    this.$message({
                        message: resDate.data.sinfo,
                        type: 'success',
                        duration: 2000,
                    });  
