                        }
                    }
                    this.formSetmeal.Actual_payment = this.toFixed_fun(parseFloat(this.formSetmeal.original_price - this.toFixed_fun(this.formSetmeal.original_price * (val/100)))); // 
                    this.formSetmeal.Discount_mony = this.toFixed_fun(this.formSetmeal.original_price * (val/100))// 
                }
              }
          } else {
            // 
            this.formSetmeal.Actual_payment = this.formSetmeal.original_price   //  = 
            this.formSetmeal.Discount_mony = '0.00'// 
          }
      },
      'formSetmeal.DiscountActive3'(val){
          if (val != '') {
             if (this.formSetmeal.Totalprice == '') {
                  //  -  
                  this.$message({
                    message: '',
                    type: 'error',
                    duration: 2000,
                  });
                this.formSetmeal.DiscountActive2 = ""
              } else {
                if (!/^\d*\.{0,1}\d{0,2}$/.test(val)) {
                    this.formSetmeal.DiscountActive3 = val.substring(0, val.length-1);
                    this.formSetmeal.Actual_payment =  this.toFixed_fun(this.formSetmeal.original_price - val) // 
                    this.formSetmeal.Discount_mony =  val // 
                } else {
                    if (parseFloat(val) > parseFloat(this.formSetmeal.Totalprice)) {
                        this.$message({
                            message: '!',
                            type: 'error',
                            duration: 2000,
                        });
                        this.formSetmeal.DiscountActive3 = this.formSetmeal.original_price
                        this.formSetmeal.Actual_payment = 0
                        this.formSetmeal.Discount_mony =  this.formSetmeal.DiscountActive3
                    } else {
                        this.formSetmeal.Actual_payment =  this.toFixed_fun(this.formSetmeal.original_price - val) // 
                        this.formSetmeal.Discount_mony =  val // 
                    }
                }
              }
          } else {
            // 
