                type_num: '',
                Class_hour: '',// 
                Termvalidity: '', // 
                thisDate: '', // 
                shopClass:'',// 
                DiscountActive2: '', // 
                DiscountActive3: '', // 
                FreeClassHours: 0, // 
                Actual_payment: '', // 
                Activemethod: '-1',// 
                original_price: '0.00', // 
                Discount_mony: '0.00' // 
            },
