export default {
	layerOpen:function(b) {
		var frameDiv;
		$(".layOpenSpan").remove()
		layer.closeAll();
		if(!frameDiv){
			frameDiv = document.createElement("span");//
			var body1 = document.getElementsByTagName('body');
			document.body.appendChild(frameDiv)
			frameDiv.className="layOpenSpan";
			frameDiv.innerHTML = b.path[0].innerText;
			frameDiv.style.display="none";	
		}
		else{
			frameDiv.innerHTML = b.path[0].innerText;	
		}
		var pt = $(b.path[1]).width();
		var t = b.currentTarget.getBoundingClientRect();
		let w = $(".layOpenSpan").width()
		if (pt < w) {
			var that = b.currentTarget;
			var text = b.path[0].innerText;
			var l = t.x-10+'px';
			var t = t.y-10+'px';
			layer.open({
				type: 1,
				fixed: false,
				icon: 2, 
				offset: [t,l],   //
				shade: false,
				title: false, //
				area: [pt, 'auto'],
				content: text, //body
				success: function(layero,index){
				    layero.css({"min-width":pt,"background-color":"#fff","border-radius": "6px"}).append(layero);    //
				    layero[0].children[0].style.margin = "10px"
				    layero[0].children[0].style.height = "auto"
				    layero[0].children[0].style.maxWidth = "280px"
				    layero[0].children[0].style.overflowX = "hidden"
				    layero[0].children[0].style.wordBreak = "break-word"
				  }
				});
		}      	
	},
}
